<template>
  <el-select v-model="value" placeholder="请选择故障处理人员">
    <el-option
      v-for="item in selectSever"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: 'selectServrer',
  props: ['selectSever'],
  data () {
    return {
      options: [{
        value: '1',
        label: '张一'
      }, {
        value: '2',
        label: '张二'
      }, {
        value: '3',
        label: '张三'
      }, {
        value: '4',
        label: '张四'
      }, {
        value: '5',
        label: '张五'
      }],
      value: ''
    }
  }
}
</script>

<style scoped>

</style>



