<template>
    <div>
      <el-table border :data="tableData" style="width: 98%;margin-top: 15px;">
        <el-table-column type="index" label="序号" width="60px" align="center" ></el-table-column>
        <el-table-column prop="plotName" label="小区名称" min-width="20%" align="center"></el-table-column>
        <el-table-column prop="monitorName" label="运维人员" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="servicerName" label="售后人员" min-width="20%" align="center"></el-table-column>
        <el-table-column prop="operation" label="操作" min-width="10%" align="center" >
          <template slot-scope="scope">
            <el-button type="primary" style="padding:10px 15px 10px 15px;"
                       @click="setUpDeviceMS(scope.$index, scope.row)">设置
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog title="设置监控售后负责人" :visible.sync="dialogFormVisible">
        <el-form :label-position="labelPosition" label-width="150px">
          <el-form-item label="小区名称">
            <el-input v-model="form.plotName" autocomplete="on" disabled></el-input>
          </el-form-item>
          <el-form-item label="原运维人员">
            <el-input v-model="form.originMonitor" autocomplete="on" disabled></el-input>
          </el-form-item>
          <el-form-item label="原售后人员">
            <el-input v-model="form.originServicer" autocomplete="on" disabled></el-input>
          </el-form-item>
          <el-form-item label="新运维人员">
            <select-people :selectSever="monitor" />
          </el-form-item>
          <el-form-item label="新售后人员">
            <select-people :selectSever="servicer" />
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
        </div>
      </el-dialog>
    </div>
</template>

<script>
import selectPeople from  './selectServrer'
export default {
  name: "deviceMonitor",
  components: { selectPeople },
  data () {
    return {
      tableData: [{
        plotName: '荆州新天地',
        monitorName: '张三',
        servicerName: '王五'
      }, {
        plotName: '荆门新天城',
        monitorName: '李恬',
        servicerName: '陈凯'
      }],
      labelPosition: 'right',
      dialogFormVisible: false,
      form: {
        plotName: null,
        plotNum: null,
        originMonitor: null,
        originServicer: null,
        newMonitor: null,
        newServicer: null,
      },
      monitor: [{
        value: '1',
        label: '张一',
        id: 10
      }, {
        value: '2',
        label: '张二',
        id: 11
      }],
      servicer: [{
        value: '1',
        label: '李一',
        id: 10
      }, {
        value: '2',
        label: '里二',
        id: 11
      }]
    }
  },
  methods: {
    setUpDeviceMS (index, row) {
      console.log(index, row)
      this.form.plotName = row.plotName
      this.form.originMonitor = row.monitorName
      this.form.originServicer = row.servicerName
      this.dialogFormVisible = true
    }
  }
}
</script>

<style scoped>

</style>



